import React, { Fragment, Component } from "react";
import Slider from "react-slick";
import bg1 from "../../../assets/utils/images/originals/city.jpg";
import bg2 from "../../../assets/utils/images/originals/citydark.jpg";
import bg3 from "../../../assets/utils/images/originals/citynights.jpg";
import { Col, Row, Button, Form, FormGroup, Label, Input } from "reactstrap";
import { Link } from 'react-router-dom';
import Loading from "../../Loading";
import Alert from '../../Alert';
import ApiCall from "../../Services/Function/ApiCall";
import {
  setLoaderFlag,
  setNotificationFlag,
  setUserData
} from '../../../reducers/ThemeOptions';
import {connect} from 'react-redux';
class ForgotPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email:null,
      message:'Please Check Your Email Address',
      type:'success',
    };
  }
  HandleSetState=(key,value)=>
  {
    this.setState({
      [key]:value
    });
  }
  HandleSubmit=async(e)=>
  {
    e.preventDefault();
    if(this.state.email)
    {
      this.toggleEnableSubmit();
      let data={
        email:this.state.email
      }
      let response=await ApiCall.postAPICall(2,'forget',data);
      let { setNotificationFlag } = this.props;
      setNotificationFlag(true);
      this.toggleEnableSubmit();
    }
  }
  toggleEnableSubmit = () => {
    let { loaderFlag, setLoaderFlag } = this.props;
    setLoaderFlag(!loaderFlag);
  };
  render() {
    let settings = {
      dots: true,
      infinite: true,
      speed: 500,
      arrows: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: true,
      initialSlide: 0,
      autoplay: true,
      adaptiveHeight: true,
    };
    return (
      <Fragment>
        <Alert type={this.state.type} message={this.state.message}/>
        <div className="h-100">
          <Row className="h-100 no-gutters">
            <Col lg="4" className="d-none d-lg-block">
              <div className="slider-light">
                <Slider {...settings}>
                  <div className="h-100 d-flex justify-content-center align-items-center bg-plum-plate">
                    <div className="slide-img-bg"
                      style={{
                        backgroundImage: "url(" + bg1 + ")",
                      }}/>
                    <div className="slider-content">
                      <h3>Perfect Balance</h3>
                      <p>
                        ArchitectUI is like a dream. Some think it's too good to
                        be true! Extensive collection of unified React Boostrap
                        Components and Elements.
                      </p>
                    </div>
                  </div>
                  <div className="h-100 d-flex justify-content-center align-items-center bg-premium-dark">
                    <div className="slide-img-bg"
                      style={{
                        backgroundImage: "url(" + bg3 + ")",
                      }}/>
                    <div className="slider-content">
                      <h3>Scalable, Modular, Consistent</h3>
                      <p>
                        Easily exclude the components you don't require.
                        Lightweight, consistent Bootstrap based styles across
                        all elements and components
                      </p>
                    </div>
                  </div>
                  <div className="h-100 d-flex justify-content-center align-items-center bg-sunny-morning">
                    <div className="slide-img-bg opacity-6"
                      style={{
                        backgroundImage: "url(" + bg2 + ")",
                      }}/>
                    <div className="slider-content">
                      <h3>Complex, but lightweight</h3>
                      <p>
                        We've included a lot of components that cover almost all
                        use cases for any type of application.
                      </p>
                    </div>
                  </div>
                </Slider>
              </div>
            </Col>
            <Col lg="8" md="12"
              className="h-100 d-flex bg-white justify-content-center align-items-center">
              <Col lg="6" md="8" sm="12" className="mx-auto app-login-box">
                <div className="app-logo" />
                <h4>
                  <div>Forgot your Password?</div>
                  <span>Use the form below to recover it.</span>
                </h4>
                <div>
                  <Form>
                    <Row form>
                      <Col md={12}>
                        <FormGroup>
                          <Label for="exampleEmail">Email</Label>
                          <Input type="email" name="email" onChange={(e)=>{this.HandleSetState('email',e.target.value)}} id="exampleEmail" placeholder="Email here..."/>
                        </FormGroup>
                      </Col>
                    </Row>
                    <div className="mt-4 d-flex align-items-center">
                      <h6 className="mb-0">
                        <Link to="/user/login">
                          <a className="text-primary">
                            Sign in existing account
                          </a>
                        </Link>
                      </h6>
                      <div className="ml-auto">
                        {!this.props.loaderFlag ?
                          <Button color="primary" size="lg" onClick={this.HandleSubmit }>
                            Recover Password
                          </Button>
                          :
                          <Loading/>
                        }
                      </div>
                    </div>
                  </Form>
                </div>
              </Col>
            </Col>
          </Row>
        </div>
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  loaderFlag: state.ThemeOptions.loaderFlag,
  notificationFlag:state.ThemeOptions.notificationFlag,
  userData:state.ThemeOptions.userData,
});

const mapDispatchToProps = dispatch => ({
  setLoaderFlag: enable => dispatch(setLoaderFlag(enable)),
  setNotificationFlag:enable=>dispatch(setNotificationFlag(enable)),
  setUserData:enable=>dispatch(setUserData(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ForgotPassword);
