import React, { Fragment, Component } from "react";

import Slider from "react-slick";
import { Loader, Types } from "react-loaders";
import LoadingOverlay from "react-loading-overlay";
import SweetAlert from "sweetalert-react";
import bg3 from "../../../assets/utils/images/originals/citynights.jpg";
import MyGlobleSetting from '../../MyGlobleSetting';
import Modal from './Modal';
import { Col, Row, Button, Form, FormGroup, Label, Input} from "reactstrap";
import { Link } from 'react-router-dom';
import {
  setLoaderFlag,
  setNotificationFlag,
  setClientWizard,
  setModalEnable,
} from '../../../reducers/ThemeOptions';
import ApiCall from "../../Services/Function/ApiCall";
import {connect} from 'react-redux';
class Register extends Component {
  constructor(props)
  {
    super(props);
    this.state={
      url:MyGlobleSetting.state.url,
      unexpectedError:MyGlobleSetting.state.unexpectedError,
      loaderType:MyGlobleSetting.state.loaderType,
      error_username:'',
      error_confirmpassword:null,
      error_email:null,
      error_password:null,
      show:null,
      email:null,
      password:null,
      username:null,
      confirm_password:null,
      term:false,
      enable_term:null,
      token:null,
    };
  }
  async componentDidMount() {
    this.setState({
      token: this.props.match.params.id
    });
    let data={
      'token':this.props.match.params.id
    }
    let response=await ApiCall.postAPICall(2,'get/email',data);
    this.setState({
      email: response.email
    });
  }
  toggleEnableSubmit = () => {
    let { loaderFlag, setLoaderFlag } = this.props;
    setLoaderFlag(!loaderFlag);
  };
  handleuserName =(e)=>{
    this.setState({
      username: e.target.value
    });
    this.handleCheckField('username');
  };
  toggleEnableShow = () => {
    let { notificationFlag, setNotificationFlag } = this.props;
    setNotificationFlag(!notificationFlag);
  };
  handleEmail=(e)=>{
    e.preventDefault();
    // this.setState({
    //   email: e.target.value
    // })
    // this.handleCheckField('email');
  };
  handleTerm=async(e)=>
  {
    await this.setState({
      term: !this.state.term
    })
    if(this.state.term)
    {
      this.setState({
        enable_term:null,
      })  
    }
  }
  handleSetTermMessag=(e)=>
  {
    this.setState({
      enable_term: 'Please Accept The Terms And Conditions'
    })
  }
  handlePassword=(e)=>{
    this.setState({
      password: e.target.value
    });
    this.handleCheckField('password');
    this.handleCheckPassword(e.target.value,this.state.confirm_password);
  };
  handleConfirmPassword=(e)=>
  {
    this.setState({
      confirm_password: e.target.value
    });
    this.handleCheckPassword(this.state.password,e.target.value);
  };
  handleCheckField=(key)=>
  {
    let errorKey='error_'+key;
    this.setState({[errorKey]: '' })
  };
  handleCheckPassword=(password,confirm_password)=>{
    if (typeof password !== "undefined" && typeof confirm_password !== "undefined" && password != confirm_password ) { 
      this.setState({error_confirmpassword : "Passwords don't match" });
      this.setState({error_password : "Passwords don't match" });
    }
    else{
      this.setState({error_confirmpassword : '' });
      this.setState({error_password : '' });
    } 
  };
  handleSubmit=async(e)=>{
    e.preventDefault();
    if(!this.state.term)
    {
      this.handleSetTermMessag();
      return ;
    } 
    const data = {
      token: this.state.token,
      password: this.state.password,
    }
    this.toggleEnableSubmit();
    let response=await ApiCall.postAPICall(2,'register',data);
    if(response.status)
    {
      localStorage.setItem('user', JSON.stringify(response.success.user));
      localStorage.setItem('isLogin', true);
      this.props.history.push("/dashboards/index");
    }
    else
    {
      const errors = response.error;
      Object.keys(errors).map(keyName=> {
        this.setState({[keyName]: errors[keyName] })
      });
    }
    this.toggleEnableSubmit();
  }
  HandleTermPage=()=>
  {
    let {modalEnable,setModalEnable } = this.props;
    setModalEnable(!modalEnable);
  }
  render() {
    let settings = {
      dots: true,
      infinite: true,
      speed: 500,
      arrows: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: true,
      initialSlide: 0,
      autoplay: true,
      adaptiveHeight: true,
    };
    let {
      notificationFlag,
      loaderFlag
    } = this.props;
    return (
      <Fragment>
        <Modal />
        <SweetAlert confirmButtonColor="" confirmButtonText="Close" title={this.state.unexpectedError} show={notificationFlag} type="error" onConfirm={() => this.toggleEnableShow()}/>
        <div className="h-100">
          <Row className="h-100 no-gutters">
            <Col lg="7" md="12" className="h-100 d-flex bg-white justify-content-center align-items-center">
              <Col lg="9" md="10" sm="12" className="mx-auto app-login-box">
                <div className="app-logo" />
                <h4>
                  <div>Welcome,</div>
                  <span>
                    It only takes a{" "}
                    <span className="text-success">few seconds</span> to create
                    your account
                  </span>
                </h4>
                <div>
                  <Form onSubmit={this.handleSubmit}>
                    <Row form>
                      {<Col md={6}>
                        <FormGroup>
                          <Label for="exampleEmail">
                            <span className="text-danger">*</span> Email
                          </Label>
                          <Input readOnly onChange={this.handleEmail} type="email" name="email" id="exampleEmail" value={this.state.email}/>
                          {<span className="text-danger">{this.state.error_email}</span>}
                        </FormGroup>
                      </Col>}
                      {/*<Col md={6}>
                        <FormGroup>
                          <Label for="exampleUserName">
                            <span className="text-danger">*</span> Username
                          </Label>
                          <Input onChange={this.handleuserName} type="text" name="text" id="exampleUserName" placeholder="Username here..."/>
                          {<span className="text-danger">{this.state.error_username}</span>}
                        </FormGroup>
                      </Col>*/}
                      <Col md={6}>
                        <FormGroup>
                          <Label for="examplePassword">
                            <span className="text-danger">*</span> Password
                          </Label>
                          <Input onChange={this.handlePassword}  type="password" name="password" id="examplePassword" placeholder="Password here..."/>
                          {<span className="text-danger">{this.state.error_password}</span>}
                        </FormGroup>
                      </Col>
                      <Col md={6}>
                        <FormGroup>
                          <Label for="examplePasswordRep">
                            <span className="text-danger">*</span> Repeat
                            Password
                          </Label>
                          <Input type="password" onChange={this.handleConfirmPassword} name="passwordrep" id="examplePasswordRep" placeholder="Repeat Password here..."/>
                          {<span className="text-danger">{this.state.error_confirmpassword}</span>}
                        </FormGroup>
                      </Col>
                    </Row>
                    <FormGroup className="mt-3" check>
                      <Input type="checkbox" onChange={this.handleTerm} name="check" id="exampleCheck" />
                      <Label for="exampleCheck" check>
                        Accept our{" "}
                        <a >
                          Terms and Conditions
                        </a>
                        .
                      </Label>
                      <a className="text-info" onClick={this.HandleTermPage} >
                        <i class="lnr lnr-eye"></i>
                      </a>
                      {<span className="text-danger"><br></br>{this.state.enable_term}</span>}
                    </FormGroup>
                    <div className="mt-4 d-flex align-items-center">
                      <h5 className="mb-0">
                        Already have an account?{" "}
                        <a href="#/user/login" className="text-primary">
                          Sign in
                        </a>
                      </h5>
                      <div className="ml-auto">
                      {!loaderFlag ?
                        <Button color="primary" className="btn-wide btn-pill btn-shadow btn-hover-shine" size="lg">
                          Create Account
                        </Button>
                        : <LoadingOverlay tag="div" active={loaderFlag}
                          styles={{
                            overlay: (base) => ({
                              ...base,
                              background: "#fff",
                              opacity: 0.5,
                            }),
                          }}
                          spinner={<Loader active type={this.state.loaderType} />}>
                        </LoadingOverlay>
                      }
                      </div>
                    </div>
                  </Form>
                </div>
              </Col>
            </Col>
            <Col lg="5" className="d-none d-lg-flex d-xs-none">
              <div className="slider-light">
                <Slider {...settings}>
                  <div className="h-100 d-flex justify-content-center align-items-center bg-premium-dark">
                    <div className="slide-img-bg"
                      style={{
                        backgroundImage: "url(" + bg3 + ")",
                      }}/>
                    <div className="slider-content">
                      <h3>Scalable, Modular, Consistent</h3>
                      <p>
                        Easily exclude the components you don't require.
                        Lightweight, consistent Bootstrap based styles across
                        all elements and components
                      </p>
                    </div>
                  </div>
                </Slider>
              </div>
            </Col>
          </Row>
        </div>
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  loaderFlag: state.ThemeOptions.loaderFlag,
  notificationFlag:state.ThemeOptions.notificationFlag,
  clientWizard:state.ThemeOptions.clientWizard,
  modalEnable:state.ThemeOptions.modalEnable,
});

const mapDispatchToProps = dispatch => ({
  setLoaderFlag: enable => dispatch(setLoaderFlag(enable)),
  setNotificationFlag:enable=>dispatch(setNotificationFlag(enable)),
  setClientWizard:enable=>dispatch(setClientWizard(enable)),
  setModalEnable:enable=>dispatch(setModalEnable(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Register);
