import React, { Fragment, Component } from "react";

import Slider from "react-slick";

import bg1 from "../../../assets/utils/images/originals/city.jpg";
import bg2 from "../../../assets/utils/images/originals/citydark.jpg";
import bg3 from "../../../assets/utils/images/originals/citynights.jpg";

import { Col, Row, Button, Form, FormGroup, Label, Input } from "reactstrap";
import { Loader, Types } from "react-loaders";
import LoadingOverlay from "react-loading-overlay";
import SweetAlert from "sweetalert-react";
import MyGlobleSetting from '../../MyGlobleSetting';
import { Link } from 'react-router-dom';
import {
  setLoaderFlag,
  setNotificationFlag,
  setPermissionData
} from '../../../reducers/ThemeOptions';
import { toast, Bounce } from "react-toastify";
import ApiCall from "../../Services/Function/ApiCall";
import {connect} from 'react-redux';
class Login extends Component {
  constructor(props)
  {
    super(props);
    this.state={
      url:MyGlobleSetting.state.url,
      unexpectedError:MyGlobleSetting.state.unexpectedError,
      loaderType:MyGlobleSetting.state.loaderType,
      error_email:'',
      error_password:'',
    };
  }
  toggleEnableSubmit = () => {
      let { loaderFlag, setLoaderFlag } = this.props;
      setLoaderFlag(!loaderFlag);
    };
    toggleEnableShow = () => {
      let { notificationFlag, setNotificationFlag } = this.props;
      setNotificationFlag(!notificationFlag);
    };
    handleEmail = (e)=>{
      this.setState({
        email: e.target.value
      });
    };
    handlePassword = (e)=>{
      this.setState({
        password:e.target.value
      });
    };
    handleSubmit=async(e)=>{
      e.preventDefault();
      const data = {
        email: this.state.email,
        password: this.state.password,
        deviceID:"abcffdfddf"
      }
      // localStorage.setItem('Bearertoken', JSON.stringify(Bearertoken));
          // var response=await ApiCall.postAPICall(1,'login',data);
          // if(response.status)
          // {
          //   this.setState({isLogin:true});
          //   localStorage.setItem('user', JSON.stringify(response.data));
          //   // var token="Bearer "+response.custom;
          //   var token=response.custom;
          //   localStorage.setItem('token', JSON.stringify(token));
          //   let { permissionData, setPermissionData } = this.props;
          //   await setPermissionData(JSON.stringify(response.permisions));
          //   localStorage.setItem('permisions', JSON.stringify(response.permisions));
          //   localStorage.setItem('isLogin', true);
          //   this.props.history.push("/dashboards/index");
          // }
          // return true;
       // var response_login=await ApiCall.postAPICall(1,'login',data);
       // console.log(response_login);
       // return true;
      this.toggleEnableSubmit();
      let login_response=await ApiCall.postAPICall(2,'Account/Authenticate',data);
      if(typeof(login_response.token) == 'undefined' )
      {
        var message="Something Went Wrong..!"
        if(typeof(login_response.message) != 'undefined')
        {
          message=login_response.message;
        }
        (this.toastId = toast(
          message,
          {
            transition: Bounce,
            closeButton: true,
            autoClose: 2000,
            position: "bottom-center",
            type: "error",
            bodyOutputType: 'trustedHtml'
          }

        ))
      }
      else{
          var Bearertoken="Bearer "+login_response.token;
          localStorage.setItem('Bearertoken', JSON.stringify(Bearertoken));
          var response=await ApiCall.postAPICall(1,'login',data);
          if(response.status)
          {
            this.setState({isLogin:true});
            localStorage.setItem('user', JSON.stringify(response.data));
            // var token="Bearer "+response.custom;
            var token=response.custom;
            localStorage.setItem('token', JSON.stringify(token));
            let { permissionData, setPermissionData } = this.props;
            await setPermissionData(JSON.stringify(response.permisions));
            localStorage.setItem('permisions', JSON.stringify(response.permisions));
            localStorage.setItem('isLogin', true);
            this.props.history.push("/dashboards/index");
          }
          else
          {
            const errors = response.error;
            this.setState({error_email: errors });
          }

      }
      this.toggleEnableSubmit();
    }
  render() {
    let {
      notificationFlag,
      loaderFlag
    } = this.props;
    let settings = {
      dots: true,
      infinite: true,
      speed: 500,
      arrows: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: true,
      initialSlide: 0,
      autoplay: true,
      adaptiveHeight: true,
    };
    return (
      <Fragment>
        <SweetAlert confirmButtonColor="" confirmButtonText="Close" title={this.state.unexpectedError} show={notificationFlag} type="error" onConfirm={() => this.toggleEnableShow()}/>
        <div className="h-100">
          <Row className="h-100 no-gutters">
            <Col lg="4" className="d-none d-lg-block">
              <div className="slider-light">
                <Slider {...settings}>
                  <div className="h-100 d-flex justify-content-center align-items-center bg-plum-plate">
                    <div className="slide-img-bg"
                      style={{
                        backgroundImage: "url(" + bg1 + ")",
                      }}/>
                    <div className="slider-content">
                      <h3>Perfect Balance</h3>
                      <p>
                        ArchitectUI is like a dream. Some think it's too good to
                        be true! Extensive collection of unified React Boostrap
                        Components and Elements.
                      </p>
                    </div>
                  </div>
                  <div className="h-100 d-flex justify-content-center align-items-center bg-premium-dark">
                    <div className="slide-img-bg"
                      style={{
                        backgroundImage: "url(" + bg3 + ")",
                      }}/>
                    <div className="slider-content">
                      <h3>Scalable, Modular, Consistent</h3>
                      <p>
                        Easily exclude the components you don't require.
                        Lightweight, consistent Bootstrap based styles across
                        all elements and components
                      </p>
                    </div>
                  </div>
                  <div className="h-100 d-flex justify-content-center align-items-center bg-sunny-morning">
                    <div className="slide-img-bg opacity-6"
                      style={{
                        backgroundImage: "url(" + bg2 + ")",
                      }}/>
                    <div className="slider-content">
                      <h3>Complex, but lightweight</h3>
                      <p>
                        We've included a lot of components that cover almost all
                        use cases for any type of application.
                      </p>
                    </div>
                  </div>
                </Slider>
              </div>
            </Col>
            <Col lg="8" md="12" className="h-100 d-flex bg-white justify-content-center align-items-center">
              <Col lg="9" md="10" sm="12" className="mx-auto app-login-box">
                <div className="app-logo" />
                <h4 className="mb-0">
                  <div>Welcome back,</div>
                  <span>Please sign in to your account.</span>
                </h4>
                {/*<h6 className="mt-3">
                  No account?{" "}
                  <a href="#/user/register" className="text-primary" >
                    Sign up now
                  </a>
                </h6>*/}
                <Row className="divider" />
                <div>
                  <Form onSubmit={this.handleSubmit}>
                    <Row form>
                      <Col md={6}>
                        <FormGroup>
                          <Label for="exampleEmail"><span className="text-danger">*</span> Email</Label>
                          <Input type="email" onChange={this.handleEmail} name="email" id="exampleEmail" placeholder="Email here..."/>
                          {<span className="text-danger">{this.state.error_email}</span>}
                        </FormGroup>
                      </Col>
                      <Col md={6}>
                        <FormGroup>
                          <Label for="examplePassword"> <span className="text-danger">*</span> Password</Label>
                          <Input type="password" onChange={this.handlePassword} name="password" id="examplePassword" placeholder="Password here..."/>
                        </FormGroup>
                      </Col>
                    </Row>
                    {/*<FormGroup check>
                      <Input type="checkbox" name="check" id="exampleCheck" />
                      <Label for="exampleCheck" check>
                        Keep me logged in
                      </Label>
                    </FormGroup>*/}
                    <Row className="divider" />
                    <div className="d-flex align-items-center">
                      <div className="ml-auto">
                        {!loaderFlag ?
                          <Fragment>
                            <a href="#/user/forgot-password" className="btn-lg btn btn-link" >
                            Recover Password</a>
                            <Button color="primary" size="lg">
                              Login to Dashboard
                            </Button>
                          </Fragment> 
                        : <LoadingOverlay tag="div" active={loaderFlag}
                          styles={{
                            overlay: (base) => ({
                              ...base,
                              background: "#fff",
                              opacity: 0.5,
                            }),
                          }}
                          spinner={<Loader active type={this.state.loaderType} />}>
                        </LoadingOverlay>
                      }
                      </div>
                    </div>
                  </Form>
                </div>
              </Col>
            </Col>
          </Row>
        </div>
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  loaderFlag: state.ThemeOptions.loaderFlag,
  notificationFlag:state.ThemeOptions.notificationFlag,
  permissionData:state.ThemeOptions.permissionData,
});

const mapDispatchToProps = dispatch => ({
  setLoaderFlag: enable => dispatch(setLoaderFlag(enable)),
  setPermissionData: enable => dispatch(setPermissionData(enable)),
  setNotificationFlag:enable=>dispatch(setNotificationFlag(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Login);
