import React, { Component, Fragment } from "react";
import { Loader, Types } from "react-loaders";
import LoadingOverlay from "react-loading-overlay";
import MyGlobleSetting from '../MyGlobleSetting';
import SweetAlert from "sweetalert-react";
import {
  setNotificationFlag
} from '../../reducers/ThemeOptions';
import {connect} from 'react-redux';
class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }
  toggleEnableShow = () => {
    let { notificationFlag, setNotificationFlag } = this.props;
    setNotificationFlag(!notificationFlag);
  };
  render() {
    return (
      <Fragment>
        <SweetAlert confirmButtonColor="" confirmButtonText="Close" title={this.props.message} show={this.props.notificationFlag} type={this.props.type} onConfirm={() => this.toggleEnableShow()}/>
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  notificationFlag:state.ThemeOptions.notificationFlag,
});

const mapDispatchToProps = dispatch => ({
  setNotificationFlag:enable=>dispatch(setNotificationFlag(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(index);
